import request from '@/utils/request'
// 登录
export const login = (data) => {
  return request.post('/v1_0/authorizations', data)
}
// 发送验证码 每个手机号每分钟1次
export const sendSms = (mobile) => {
  return request.get(`/v1_0/sms/codes/${mobile}`)
}
// 获取用户信息
export const getUserInfo = () => {
  return request.get('/v1_0/user')
}
// 获取用户频道列表
export const getUserChannels = () => {
  return request.get('/v1_0/user/channels')
}
// 关注用户
export const followUser = (target) => {
  return request.post('/v1_0/user/followings', { target })
}
// 取消关注
export const unFollowUser = (target) => {
  return request.delete(`/v1_0/user/followings/${target}`)
}
// 获取用户自己信息
export const getUsermsg = () => {
  return request.get('/v1_0/user/profile')
}
// 更改用户头像
export const editPhoto = (photo) => {
  // 如果请求体是表单对象，也不用自己添加content-type
  // axios发现请求体是表单对象，而是交给浏览器，浏览器发现请求提示formData会自己携带content-type
  // application/json是axios携带，前提：data是对象，内部转换JSON，发送给后台
  // multipart/form-data浏览器携带，前提：data请求体必须是formData类型对象
  return request.patch('/v1_0/user/photo', photo)
}
// 更改用户信息
export const editUsermsg = (dataObj) => {
  // 如果请求体是表单对象，也不用自己添加content-type
  // axios发现请求体是表单对象，而是交给浏览器，浏览器发现请求提示formData会自己携带content-type
  // application/json是axios携带，前提：data是对象，内部转换JSON，发送给后台
  // multipart/form-data浏览器携带，前提：data请求体必须是formData类型对象
  // data不会忽略值为null的那对keyvalue，params遇到null值会忽略不携带此对参数和值带给后台
  // 进行判断
  // 判断每队keyvalue，如果是undefinded删除此对keyvalue

  // 写法1: 解构赋值, 4个判断, 往空对象上添加有值的加上去(以前做过)
  // 写法2: 外面想传几对象key+value, 就直接传入交给后台
  // 写法3: 上面写法不够语义化, 我看不出obj里有什么

  // 高级写法
  const obj = {
    name: '', // 昵称
    gender: 0, // 性别 0男1女
    birthday: '', // 生日格式'2018-12-20'
    real_name: '', // 	真实姓名
    intro: ''
  }
  for (const k in obj) {
    // 遍历参数对象里每个key
    if (dataObj[k] === undefined) {
      // 用key去外面传入的参数对象匹配, 如果没有找到(证明外面没传这个参数)
      delete obj[k] // 从obj身上移除这对属性和值
    } else {
      obj[k] = dataObj[k] // 如果使用了, 就从外面对象取出对应key值, 保存到obj上
    }
  }
  // name, // 昵称
  // gender, // 性别 0男1女
  // birthday, // 生日格式'2018-12-20'
  // real_name, // 	真实姓名
  // intro // 个人介绍
  return request.patch('/v1_0/user/profile', obj)
}
